import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:8000';

//    axios
const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json'
  }
});

//      
api.interceptors.request.use(config => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

//  
export const registerUser = async (username, password) => {
  try {
    const response = await api.post('/register', { username, password });
    return response.data;
  } catch (error) {
    throw error.response ? error.response.data : new Error(' ');
  }
};

//   
export const getToken = async (username, password) => {
  try {
    const formData = new FormData();
    formData.append('username', username);
    formData.append('password', password);

    const response = await fetch(`${API_URL}/token`, {
      method: 'POST',
      body: formData
    });
   
    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.detail || ' ');
    }
   
    return await response.json();
  } catch (error) {
    console.error('   :', error);
    throw error;
  }
};

//  
export const logout = () => {
  localStorage.removeItem('token');
};

//  
export const isAuthenticated = () => {
  return localStorage.getItem('token') !== null;
};

//    LLM   RAG
export const askDirectQuestion = async (question) => {
  try {
    const token = localStorage.getItem('token');
    if (!token) {
      throw new Error(' ');
    }

    const response = await api.post('/direct-ask', { question });
    return response.data;
  } catch (error) {
    console.error('    :', error);
    throw error.response ? error.response.data : new Error('   ');
  }
};

//    RAG        
export const askQuestion = async (question) => {
  try {
    const response = await api.post('/ask', { question });
    return response.data;
  } catch (error) {
    console.error('   :', error);
   
    //     RAG,   
    if (error.response &&
        error.response.data &&
        error.response.data.detail &&
        error.response.data.detail.includes('Input/output error')) {
      console.warn(' RAG,      LLM');
      return await askDirectQuestion(question);
    }
   
    throw error.response ? error.response.data : new Error('   ');
  }
};

