import React, { createContext, useState, useEffect, useContext } from 'react';
import { isAuthenticated, getToken, logout, registerUser, getCurrentUser } from '../services/api';

//    
const AuthContext = createContext();

//  
export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  //    
  useEffect(() => {
    const checkAuth = async () => {
      try {
        if (isAuthenticated()) {
          //      
          try {
            const userData = await getCurrentUser();
            setUser({
              isAuthenticated: true,
              id: userData.id,
              username: userData.username,
              role: userData.role || 'user', //    
              created_at: userData.created_at
            });
          } catch (userError) {
            console.error("   :", userError);
            //     ,   
            setUser({ isAuthenticated: true });
          }
        }
      } catch (err) {
        console.error("  :", err);
      } finally {
        setLoading(false);
      }
    };

    checkAuth();
  }, []);

  //  
  const handleLogin = async (username, password) => {
    setLoading(true);
    setError(null);
    try {
      const data = await getToken(username, password);
      //      
      try {
        const userData = await getCurrentUser();
        setUser({
          isAuthenticated: true,
          id: userData.id,
          username: userData.username,
          role: userData.role || 'user',
          created_at: userData.created_at
        });
      } catch (userError) {
        console.error("   :", userError);
        setUser({ isAuthenticated: true, username });
      }
      return data;
    } catch (err) {
      setError(err.message || ' ');
      throw err;
    } finally {
      setLoading(false);
    }
  };

  //  
  const handleLogout = () => {
    logout();
    setUser(null);
    setError(null);
  };

  //  
  const handleRegister = async (username, password) => {
    setLoading(true);
    setError(null);
    try {
      const data = await registerUser(username, password);
      return data;
    } catch (err) {
      setError(err.message || ' ');
      throw err;
    } finally {
      setLoading(false);
    }
  };

  // ,    
  const isAdmin = () => {
    return user?.role === 'admin';
  };

  const value = {
    user,
    loading,
    error,
    login: handleLogin,
    logout: handleLogout,
    register: handleRegister,
    isAuthenticated: user?.isAuthenticated || false,
    isAdmin: isAdmin,
    userRole: user?.role || 'user'
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

//    
export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth    AuthProvider');
  }
  return context;
};

export default AuthContext;