import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Box,
  Alert,
  Button,
  Snackbar,
  FormControlLabel,
  Switch
} from '@mui/material';
import RefreshIcon from '@mui/icons-material/Refresh';
import DeleteSweepIcon from '@mui/icons-material/DeleteSweep';
import { getDocuments, uploadDocuments, reindexDocuments, deleteDocument, clearRagIndex, deleteAllDocuments } from '../services/api';
import FileUpload from '../components/FileUpload';
import DocumentList from '../components/DocumentList';
import { useAuth } from '../contexts/AuthContext';

function DocumentsPage() {
  const [documents, setDocuments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [uploading, setUploading] = useState(false);
  const [reindexing, setReindexing] = useState(false);
  const [clearing, setClearing] = useState(false);
  const [error, setError] = useState('');
  const [notification, setNotification] = useState({ open: false, message: '', severity: 'success' });
  const [selectedIds, setSelectedIds] = useState([]);
  const [showAllDocs, setShowAllDocs] = useState(false);
  const { isAdmin } = useAuth();

  //        
  useEffect(() => {
    fetchDocuments();
  }, [showAllDocs]);

  //   
  const fetchDocuments = async () => {
    setLoading(true);
    setError('');
   
    try {
      const docs = await getDocuments(isAdmin() && showAllDocs);
      setDocuments(docs);
    } catch (err) {
      setError('    ');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  //   
  const handleUploadDocument = async (titles, files) => {
    setUploading(true);
    setError('');
   
    try {
      await uploadDocuments(titles, files);
      //      
      await fetchDocuments();
      return true;
    } catch (err) {
      const errorMessage = err.message || '   ';
      setError(errorMessage);
      throw new Error(errorMessage);
    } finally {
      setUploading(false);
    }
  };

  //  
  const handleReindexDocuments = async () => {
    setReindexing(true);
    setError('');
   
    try {
      const result = await reindexDocuments(isAdmin() && showAllDocs);
      setNotification({
        open: true,
        message: ` ${result.indexed_documents} ,  ${result.total_chunks} `,
        severity: 'success'
      });
    } catch (err) {
      const errorMessage = err.message || '   ';
      setError(errorMessage);
      setNotification({
        open: true,
        message: errorMessage,
        severity: 'error'
      });
    } finally {
      setReindexing(false);
    }
  };

  //    
  const handleClearIndex = async () => {
    if (!window.confirm('      ?      ,       .')) {
      return;
    }

    setClearing(true);
    setError('');
   
    try {
      const result = await clearRagIndex();
      setNotification({
        open: true,
        message: `    `,
        severity: 'success'
      });
    } catch (err) {
      const errorMessage = err.message || '   ';
      setError(errorMessage);
      setNotification({
        open: true,
        message: errorMessage,
        severity: 'error'
      });
    } finally {
      setClearing(false);
    }
  };

  //    (  )
  const handleDeleteAllDocuments = async () => {
    if (!window.confirm('!       .    . ?')) {
      return;
    }

    setClearing(true);
    setError('');
   
    try {
      const result = await deleteAllDocuments();
      setNotification({
        open: true,
        message: result.message || '   ',
        severity: 'success'
      });
      await fetchDocuments();
    } catch (err) {
      const errorMessage = err.message || '    ';
      setError(errorMessage);
      setNotification({
        open: true,
        message: errorMessage,
        severity: 'error'
      });
    } finally {
      setClearing(false);
    }
  };

  //  
  const handleDelete = async (docId) => {
    if (!window.confirm(' ?')) return;
    try {
      await deleteDocument(docId);
      await fetchDocuments();
      setNotification({ open: true, message: ' ', severity: 'success' });
    } catch (err) {
      setError(err.message || '   ');
      setNotification({ open: true, message: err.message || '   ', severity: 'error' });
    }
  };

  //  
  const handleCloseNotification = () => {
    setNotification({ ...notification, open: false });
  };

  //   
  const handleSelect = (id, checked) => {
    setSelectedIds(prev => checked ? [...prev, id] : prev.filter(x => x !== id));
  };
  const handleSelectAll = (checked) => {
    setSelectedIds(checked ? documents.map(d => d.id) : []);
  };
  //  
  const handleDeleteSelected = async () => {
    if (selectedIds.length === 0) return;
    if (!window.confirm(`   (${selectedIds.length})?`)) return;
    try {
      await Promise.all(selectedIds.map(id => deleteDocument(id)));
      setNotification({ open: true, message: '  ', severity: 'success' });
      setSelectedIds([]);
      await fetchDocuments();
    } catch (err) {
      setError(err.message || '   ');
      setNotification({ open: true, message: err.message || '   ', severity: 'error' });
    }
  };

  //     (  )
  const toggleShowAllDocs = (event) => {
    setShowAllDocs(event.target.checked);
  };

  return (
    <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
        <Typography variant="h4" component="h1">
           
        </Typography>
        
        <Box>
          {isAdmin() && (
            <Button
              variant="outlined"
              color="secondary"
              onClick={handleDeleteAllDocuments}
              disabled={clearing}
              sx={{ mr: 2 }}
            >
                
            </Button>
          )}
          <Button
            variant="outlined"
            color="error"
            startIcon={<DeleteSweepIcon />}
            onClick={handleClearIndex}
            disabled={clearing}
            sx={{ mr: 2 }}
          >
             
          </Button>
          <Button
            variant="outlined"
            color="primary"
            startIcon={<RefreshIcon />}
            onClick={handleReindexDocuments}
            disabled={reindexing}
          >
            
          </Button>
        </Box>
      </Box>
      
      {isAdmin() && (
        <Box sx={{ mb: 2 }}>
          <FormControlLabel
            control={
              <Switch 
                checked={showAllDocs} 
                onChange={toggleShowAllDocs}
                color="primary"
              />
            }
            label="   "
          />
        </Box>
      )}
      
      <Typography variant="body1" paragraph>
             RAG-.
          ,     .
      </Typography>
      
      {error && <Alert severity="error" sx={{ mb: 3 }}>{error}</Alert>}
      
      <Box sx={{ mb: 4 }}>
        <FileUpload
          onUpload={handleUploadDocument}
          loading={uploading}
        />
      </Box>
      
      <DocumentList
        documents={documents}
        loading={loading}
        onDelete={handleDelete}
        selectedIds={selectedIds}
        onSelect={handleSelect}
        onSelectAll={handleSelectAll}
        onDeleteSelected={handleDeleteSelected}
        showOwner={isAdmin() && showAllDocs}
      />
      
      <Snackbar
        open={notification.open}
        autoHideDuration={6000}
        onClose={handleCloseNotification}
        message={notification.message}
      />
    </Container>
  );
}

export default DocumentsPage;