import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  TextField,
  Button,
  Typography,
  Box,
  Paper,
  CircularProgress,
  Alert,
  Tabs,
  Tab
} from '@mui/material';
import { getToken, registerUser, isAuthenticated } from '../services/api';

function LoginPage({ loading: loadingProp, setLoading: setLoadingProp }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [internalLoading, internalSetLoading] = useState(false);
  const loading = loadingProp !== undefined ? loadingProp : internalLoading;
  const setLoading = setLoadingProp !== undefined ? setLoadingProp : internalSetLoading;
  const [error, setError] = useState('');
  const [tab, setTab] = useState(0); // 0 - , 1 - 
 
  const navigate = useNavigate();

  //    
  useEffect(() => {
    if (isAuthenticated()) {
      navigate('/chat');
    }
  }, [navigate]);

  const handleTabChange = (event, newValue) => {
    setTab(newValue);
    setError('');
  };

  const handleLogin = async (e) => {
    e.preventDefault();
   
    if (!username || !password) {
      setError('  ');
      return;
    }
   
    setLoading(true);
    setError('');
   
    try {
      const response = await getToken(username, password);
     
      //  
      localStorage.setItem('token', response.access_token);
     
      //    
      window.location.replace('/chat');
    } catch (error) {
      console.error('Error during login:', error);
      setError(error.message || '  .    .');
    } finally {
      setLoading(false);
    }
  };

  const handleRegister = async (e) => {
    e.preventDefault();
   
    if (!username || !password) {
      setError('  ');
      return;
    }
   
    if (username.length < 3) {
      setError('      3 ');
      return;
    }
   
    if (password.length < 6) {
      setError('     6 ');
      return;
    }
   
    setLoading(true);
    setError('');
   
    try {
      await registerUser(username, password);
     
      //     
      const response = await getToken(username, password);
      localStorage.setItem('token', response.access_token);
     
      window.location.replace('/chat');
    } catch (error) {
      console.error('Error during registration:', error);
      setError(error.message || '  . ,   .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container maxWidth="sm" sx={{ mt: 8 }}>
      <Paper elevation={3} sx={{ p: 4 }}>
        <Typography variant="h4" align="center" gutterBottom>
          RAG 
        </Typography>
       
        <Tabs value={tab} onChange={handleTabChange} centered sx={{ mb: 4 }}>
          <Tab label="" />
          <Tab label="" />
        </Tabs>
       
        {error && (
          <Alert severity="error" sx={{ mb: 3 }}>
            {error}
          </Alert>
        )}
       
        <Box component="form" onSubmit={tab === 0 ? handleLogin : handleRegister}>
          <TextField
            label=" "
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            fullWidth
            margin="normal"
            variant="outlined"
            required
            disabled={loading}
            autoFocus
            InputLabelProps={{ shrink: true }}
            data-testid="username-input"
          />
         
          <TextField
            label=""
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            fullWidth
            margin="normal"
            variant="outlined"
            required
            disabled={loading}
            InputLabelProps={{ shrink: true }}
            data-testid="password-input"
          />
         
          <Button
            type="submit"
            variant="contained"
            color="primary"
            fullWidth
            size="large"
            disabled={loading}
            sx={{ mt: 3, mb: 2 }}
            data-testid="login-button"
          >
            {loading ? (
              <CircularProgress size={24} color="inherit" />
            ) : tab === 0 ? '' : ''}
          </Button>
        </Box>
      </Paper>
    </Container>
  );
}

export default LoginPage;